
import styles from './../Contato/Contato.module.css'
import Aos from 'aos';
import 'aos/dist/aos.css'
import { useEffect } from 'react';
export default function Contato(){


    useEffect(()=>{
        Aos.init({duration: 2500});
    },[])


    return(
        <div id='contato' className={styles.container}>
           <div className={styles.form} data-aos="fade-up-right">
               <h1>Contato</h1>
               <input type={'text'} placeholder= 'Nome' className={styles.input}></input>
               <input type={'text'} placeholder= 'Email'  className={styles.input}></input>
               <input type={'text'} placeholder= 'Sua mensagem' className={styles.inputMsg}></input>

               <a className={styles.btn}>Enviar</a>
               </div>
               
        </div>
    )
}