import styles from './../About/About.module.css'
import Aos from 'aos';
import 'aos/dist/aos.css'
import { useEffect } from 'react';
import curr from './../../images/curr.pdf'

export default function About(){

    
    useEffect(()=>{
        Aos.init({duration: 2000});
    },[])

    

    
      

    return(
        <div id='sobreContainer' className={styles.container}>
            <div className={styles.card} data-aos="flip-right">
                <div className={styles.photo}></div>    
                <div className={styles.descricao}>
                    <h1>Sobre...</h1>
                    <p className={styles.text}>Ei! Meu nome  Gustavo Henrique e nos ltimos meses venho estudando programao,
                         mais especificamente programao front-end/web. Tenho desenvolvido projetos 
                         envolvendo Javascript, como alguns jogos, algumas single pages usando ReactJS, Sou 
                         apaixonado por novos desafios, proativo, paciente, 
                         gosto de trabalhar em equipe, a curiosidade me impulsiona em aprender e desenvolver 
                         coisas novas e tambm 
                         procuro sempre me desenvolver para colaborar com novas propostas, buscando crescer e evoluir todos os dias.!</p>
                    <div className={styles.curriculo}>
                        <a style={{color:'black', textDecoration:'none'}} href={curr} download={curr}>Download CV</a>
                        </div>
                    </div>

                    
            </div>
        </div>
    )
}