import logo from './logo.svg';
import './App.css';
import styled from 'styled-components';
import Menu from './components/Menu/Menu';
import { useEffect, useState } from 'react';
import styles from './../src/components/Menu/Menu.module.css'
import Sobre from './components/Sobre/Sobre';
import BackgroundAnimation from './components/animacao/BackgroundAnimation';
import About from './components/About/About';
import Habilidades from './components/Habilidades/Habilidades';
import Projeto from './components/Projeto/Projeto';
import Contato from './components/Contato/Contato';


const Container = styled.div`
  display: flex;
  background-color: black;
  height: 100vh;
  width:100%;
  text-align: center;
 
  
  
  
`





function App(props) {
  const [open, setOpen] = useState(true)
  const [width, setWidth] = useState()


  

 useEffect(() =>{
    window.addEventListener('resize', resize)
      
   
    
    return ()=>{
      window.removeEventListener('resize', resize)
    }  
 },[]) 

  const handleOpen = () =>{
    setOpen(!open)
  }

  function resize(){
    let width = window.innerWidth
    if(width > 1200){
    setOpen(true)
    }else{
      setOpen(false)
    }
  }
  
  

  return (
    <div className="App">
      <Container>
      <div onClick={handleOpen} className={styles.displayMenu}><img className={styles.btn} src="https://img.icons8.com/fluency/48/000000/menu-rounded.png"/></div>
      <Menu isOpen={open} handleOpen={handleOpen}/>
      <Sobre/>
      <BackgroundAnimation/>
      </Container>
      <About/>
      <Habilidades/>
      <Projeto/>
      <Contato/>
      
    </div>
  );
}

export default App;
