import styles from './../Projeto/Projeto.module.css'
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import image from './../../images/monty.png'
import expense from './../../images/expense.png'
import mosca from './../../images/mosca.png'
import pizzaria from './../../images/pizzaria.png'
import quiz from './../../images/quiz.png'
import api from './../../images/api.png'
import Aos from 'aos';
import 'aos/dist/aos.css'
import { useEffect } from 'react';


export let projetos = document.querySelector('#projeto') 


export default function Projeto(){
    
    function redirecionar(value){
        window.open(
        value,
        '_blank'
            )}

          
            useEffect(()=>{
                Aos.init({duration: 2500});
            },[])

     return(
        <div id='projeto' className={styles.container}>
            
           <h1>Meus Projetos!</h1>
            <div className={styles.projetos}>
           <div className={styles.box} onClick={() => redirecionar('https://github.com/guhhh25/Interface-Pizzaria')} >
               <img className={styles.imgContainer}  data-aos="fade-left"src={pizzaria}></img>
               <div className={styles.containerText} data-aos="fade-left">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>Interface Pizzaria</p>
               </div>
               </div>

               <div className={styles.box} onClick={ () => redirecionar('https://github.com/guhhh25/expense-tracker')} >
               <img className={styles.imgContainer} data-aos="fade-right" src={mosca}></img>
               <div className={styles.containerText} data-aos="fade-right">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>Sistema Financeiro</p>
               </div>
               </div>

               
               </div>

              
               <div className={styles.projetos}>
           <div className={styles.box}  onClick={ () => redirecionar('https://github.com/guhhh25/game-javascript')}>
               <img className={styles.imgContainer} data-aos="fade-left" src={expense}></img>
               <div className={styles.containerText} data-aos="fade-left">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>Game Javascript</p>
               </div>
               </div>

               <div className={styles.box} onClick={ () => redirecionar('https://github.com/guhhh25/monty-hall')} >
               <img className={styles.imgContainer} data-aos="fade-right" src={image}></img>
               <div className={styles.containerText} data-aos="fade-right">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>Monty Hall</p>
               </div>
               </div>
               

               

               
       </div>

       <div className={styles.projetos}>
           <div className={styles.box}  onClick={ () => redirecionar('https://github.com/guhhh25/quiz-')}>
               <img className={styles.imgContainer} data-aos="fade-left" src={quiz}></img>
               <div className={styles.containerText} data-aos="fade-left">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>Quiz App</p>
               </div>
               </div>

               <div className={styles.box} onClick={ () => redirecionar('https://github.com/guhhh25/API-Cep')} >
               <img className={styles.imgContainer} data-aos="fade-right" src={api}></img>
               <div className={styles.containerText} data-aos="fade-right">
               <img className={styles.img} src="https://img.icons8.com/color/48/000000/html-5--v1.png"/>
               <p>API CEP</p>
               </div>
               </div>

        </div>
        </div>

    )
}