import { useEffect, useState } from 'react'
import styles from './../Menu/Menu.module.css'
import { projetos } from './../Projeto/Projeto'



export default function Menu(props){

    function sobreRedirecionar(value){

        const sobree =  document.getElementById('sobreContainer')
        const sobre2 = sobree.offsetTop

        window.scrollTo({
            top: sobre2,
            behavior: 'smooth',
            
                  
            });
    
       }

       function projetoRedirecionar(value){

        const sobree =  document.getElementById('projeto')
        const sobre2 = sobree.offsetTop

        window.scrollTo({
            top: sobre2,
            behavior: 'smooth'
                  
            });
    
       }

       function habilidadesRedirecionar(value){

        const sobree =  document.getElementById('habilidades')
        const sobre2 = sobree.offsetTop

        window.scrollTo({
            top: sobre2,
            behavior: 'smooth'
                  
            });
    
       }

       function contatoRedirecionar(value){

        const sobree =  document.getElementById('contato')
        const sobre2 = sobree.offsetTop

        window.scrollTo({
            top: sobre2,
            behavior: 'smooth'
                  
            });
    
       }
      

    const scrollSobre = (value) =>{
        window.scrollTo({
          top: value,
          behavior: 'smooth'
                
          });
    }   
    
    


    function redirecionar(value){
        window.open(
        value,
        '_blank'
            )
    }
    
    return(
        <div className={props.isOpen ? styles.MenuOpen : styles.MenuClosed}>
            
            
            <div className={styles.photo}>
            </div>
            <p className={styles.texxt}>Front End Dev</p>
            <div className={styles.MenuOp}>
                <ul>
                    <li onClick={() =>scrollSobre(0)}><img className={styles.iconss}  src="https://img.icons8.com/ios-filled/20/000000/home.png"/>Inicio</li>
                    <li onClick={() =>sobreRedirecionar()}><img className={styles.iconss}  src="https://img.icons8.com/pastel-glyph/30/000000/person-male--v3.png"/>Sobre</li>
                    <li onClick={() =>habilidadesRedirecionar()} ><img className={styles.iconss} src="https://img.icons8.com/ios-filled/20/000000/book.png"/>Habilidades</li>
                    <li onClick={() =>projetoRedirecionar()} ><img className={styles.iconss} src="https://img.icons8.com/ios-glyphs/20/000000/project-management.png"/>Projetos</li>
                    <li onClick={() => contatoRedirecionar()}><img className={styles.iconss} src="https://img.icons8.com/ios-filled/50/000000/phone.png"/>Contato</li>
                    <div className={styles.icons}>
                    <img className={styles.icon1} onClick={() =>redirecionar('https://www.linkedin.com/in/gustavo-henrique-713a48170/')} src="https://img.icons8.com/ios-filled/0/000000/linkedin.png"/>
                    <img className={styles.icon2} onClick={() => redirecionar('https://github.com/guhhh25?tab=repositories')}  src="https://img.icons8.com/material-sharp/0/000000/github.png"/>
                    <img className={styles.icon3} onClick={() => redirecionar('https://api.whatsapp.com/send?phone=5511964269084&text=Ola!')}  src="https://img.icons8.com/ios-glyphs/30/000000/whatsapp.png"/>
                    </div>
                    
                </ul>
            
            
            </div>
            <p className={styles.copyright}> Copyright <strong>Gustavo Dev</strong></p>
            
        </div>
    )
}